

























































import { Component, Vue } from 'vue-property-decorator'
import HouseSaveAsideStep from './component/HouseSaveAsideStep.vue'
import HouseSaveBaseInfo from './component/HouseSaveBaseInfo.vue'
import HouseRoomTypeInfo from '@/views/business/house/component/HouseRoomTypeInfo.vue'
import HouseRoomInfo from '@/views/business/house/component/HouseRoomInfo.vue'
@Component({
  components: {
    HouseSaveBaseInfo,
    HouseSaveAsideStep,
    HouseRoomTypeInfo,
    HouseRoomInfo,
  },
})
export default class HouseSave extends Vue {
  private pageTitle: string = '项目信息'
  // 当前的step
  private currentStep = 1
  // 获取到projectId, 默认为-1, 测试为3
  private projectId: number | string = ''
  // projectNo
  private projectNo: number | string = ''
  // 获取roomTypeId 默认为-1, 测试为2
  private roomTypeId: number | string = ''

  /**
   * @Author roct
   * @Description 点击切换currentStep
   * @Date 18:25 2021/4/17 0017
   **/
  private nextStep(step: number, projectId: number, projectNo: string) {
    this.currentStep = step
    this.projectId = projectId
    this.projectNo = projectNo
  }
  /**
   * @Author roct
   * @Description 前往某一步
   * @Date 21:02 2021/4/24 0024
   **/
  private toStep(step: number) {
    this.currentStep = step
  }
  /**
   * @Author roct
   * @Description 保存模板成功
   * @Date 14:34 2021/4/18 0018
   **/
  private updateRoomTypeSuccess(roomTypeId: number) {
    this.currentStep += 1
    this.roomTypeId = roomTypeId
  }

  public created() {
    const query = Vue.ls.get('/house/save')
    if (query) {
      const query = Vue.ls.get('/house/save')
      if (query.type === 'edit') {
        this.projectId = query.id + ''
      }
    }
  }
}
