





































































import { Component, Prop, Vue } from 'vue-property-decorator'
@Component({
  components: {},
})
export default class HouseSaveStep extends Vue {
  @Prop({ type: Number, default: 1 })
  private currentStep!: number
  /**
   * @Author roct
   * @Description 前往某一个步骤
   * @Date 21:01 2021/4/24 0024
   **/
  private toStep(step: number) {
    const query = Vue.ls.get('/house/save')
    if (query && query.type === 'edit') {
      this.$emit('toStep', step)
    }
  }
}
